*{
  font-family: Helvetica, Arial, sans-serif;
}

body{
  padding: 0 20px 0 20px;
  
}

input{
  width: 50%;
  font-size: 1.2rem;
  color: #444444;
  padding: 5px;
}


.card{
  display: flex;
  flex-direction: column;
  width: 30%;
  border: 1px solid gray;
  border-radius: 6px;
  overflow: hidden;
  margin: 0 10px 10px 0;
}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 250px;
}

.content{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
